.timeline-section {
  position: relative;
  margin: 30px 0;
}

.timeline-section::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  height: 3px;
  background-color: #E31277;
}

.timeline-pointer {
  position: relative;
  display: flex;
  justify-content: center;
}

.timeline-pointer::before {
  position: absolute;
  content: '';
  width: 10px;
  height: 10px;
  background-color: #E31277;
  border-radius: 50%;
  top: -3.5px;
}

.timeline-pointer2 {
  position: relative;
  display: flex;
  justify-content: center;
}

.timeline-pointer2::before {
  position: absolute;
  content: '';
  width: 10px;
  height: 10px;
  background-color: white;
  border: 1px solid #E31277;
  border-radius: 50%;
  top: -3.5px;
  z-index: 1;
}

.orderlist img {
  height: 84px;
}
